import { Button,
         Card,
         CardBody, 
         CardTitle, 
         Row, 
         Col, 
         Table, 
         Collapse,
         Modal, 
         ModalHeader, 
         ModalBody, 
         ModalFooter,
         InputGroup,
         InputGroupText,
         Input
        } from "reactstrap";
import React, { useEffect, useState } from 'react';
import Pagination from './Pagination';
import Map from '../dashboard/Map';
import { DelBeaconData } from '../../store';
import { useDispatch, useSelector } from "react-redux"


const BeaconTables = (args) => {
  let BeaconData = useSelector((state)=> state.BeaconData);
  let dispatch = useDispatch();

  const [openIndex, setOpenIndex] = useState(null);
  const [modal, setModal] = useState(false);

  const toggle = () => setModal(!modal);
  const toggleCollapse = (index) => {
    if (openIndex === index) {
      setOpenIndex(null);
    } else {
      setOpenIndex(index);
    }
  };

  useEffect(()=>{
    
  },[BeaconData])
  
  return (
    <div>
      <Card>
        <CardBody>
          <CardTitle tag="h5"><b>Beacon service</b></CardTitle>
          <Table className="no-wrap mt-3 align-middle" responsive borderless>
            <thead>
              <tr>
                <th style={{textAlign:'center'}}>신호등</th>
                <th style={{textAlign:'center'}}>시간</th>
                <th style={{textAlign:'center'}}>주소</th>
                <th style={{textAlign:'center'}}></th>
              </tr>
            </thead>
            <tbody>
              {BeaconData.map((tdata, index) => (
                <React.Fragment key={index}>
                  <tr className="border-top" style={{textAlign:'center'}}>
                    <td>
                      <div className="d-flex align-items-center p-2" style={{textAlign:'center'}}>
                        <img
                          src={tdata.img}
                          className="rounded-circle"
                          alt="trafficlight"
                          width="45"
                          height="45"
                        />
                        <div className="ms-3">
                          <span className="text-muted" >{tdata.uuid}</span>
                        </div>
                      </div>
                    </td>
                    <td>{tdata.time}</td>
                    <td>{tdata.address}</td>
                    <td>
                      <button
                        onClick={() => toggleCollapse(index)}
                        className="btn btn-link"
                      >
                        {openIndex === index ? <i class="bi bi-toggle-on bi-2x"/> : <i class="bi bi-toggle-off bi-2x"/>}
                      </button>
                    </td>
                  </tr>
                  <tr>
                    <td colSpan="6">
                      <Collapse isOpen={openIndex === index}>
                      <Row>
                          <Col>
                            <div
                              style={{
                                width: '500px', // 원하는 너비 설정
                                height: '300px', // 원하는 높이 설정
                                border: '1px solid #ccc', // 테두리 스타일 설정
                                position: 'relative'
                              }}>
                                {openIndex === index && <Map initlatitude={tdata.latitude} initlongitude={tdata.longitude} latitude={tdata.latitude} longitude={tdata.longitude} zoom={18} />}
                            </div>
                          </Col>
                          <Col>
                          <Row className="p-4">
                              <Table striped bordered hover>
                                <thead>
                                  <tr>
                                    <th className="text-center">UUID</th>
                                    <th className="text-center">Latitude</th>
                                    <th className="text-center">Longitude</th>
                                  </tr>
                                </thead>
                                <tbody>
                                  <tr>
                                    <td className="text-center">{tdata.uuid}</td>
                                    <td className="text-center">{tdata.latitude}</td>
                                    <td className="text-center">{tdata.longitude}</td>
                                  </tr>
                                </tbody>
                              </Table>
                            </Row>

                            <Row>
                              <div className="d-flex justify-content-center">
                              <Button 
                                className="me-3" 
                                color="primary" 
                                onClick={toggle}>
                                수정
                              </Button>
                              <Modal isOpen={modal} toggle={toggle} {...args}>
                                <ModalHeader toggle={toggle}>수정</ModalHeader>
                                <ModalBody>
                                  <InputGroup className="mb-4">
                                    <InputGroupText>
                                      Latitude
                                    </InputGroupText>
                                    <Input />
                                  </InputGroup>

                                  <InputGroup className="mb-4">
                                    <InputGroupText>
                                      Longitude
                                    </InputGroupText>
                                    <Input />
                                  </InputGroup>

                                  <InputGroup className="mb-4">
                                    <InputGroupText>
                                      another
                                    </InputGroupText>
                                    <Input />
                                  </InputGroup>
                                </ModalBody>
                                <ModalFooter>
                                  <Button color="primary" onClick={toggle}>
                                    수정
                                  </Button>{' '}
                                  <Button color="secondary" onClick={toggle}>
                                    취소
                                  </Button>
                                </ModalFooter>
                              </Modal>
                              <Button
                                color="danger"
                                onClick={()=>{
                                  const confirmRegistration = window.confirm("정말 삭제하시겠습니까?");
                                  if (confirmRegistration) {
                                    dispatch(
                                      DelBeaconData(tdata.uuid)
                                    );
                                    alert("삭제되었습니다.");
                                  }
                                }}
                              >삭제</Button>
                              </div>
                            </Row>
                          </Col>
                        </Row>
                      </Collapse>
                    </td>
                  </tr>
                </React.Fragment>
              ))}
            </tbody>
            <Button className="position-absolute bottom-3 end-0 mb-4 me-5">등록</Button>
          </Table>
        </CardBody>
        <Pagination></Pagination>
      </Card>
    </div>
  );
};

export default BeaconTables;
